import { useEffect, useState } from "react";
import Table from "./components/Table/table";
import { firebaseDB } from "./utils/firebase";
import { doc, getDoc, updateDoc } from "firebase/firestore";
import socket from "./socket";

function App() {

  const [routes, setRoutes] = useState([])

  const getRoutes = async () => {
    const data = await getDoc(doc(firebaseDB, "data", "routes"));
    setRoutes(data.data())
  }

  useEffect(() => {
    getRoutes()
    socket.on('update', () => window.location.reload())
  }, [])

  const updateDB = async (id, data) => {
    socket.emit('change')
    let obj = {}
    obj[id] = data
    updateDoc(doc(firebaseDB, "data", "routes"), obj)
  }

  return (
    <div className="App">
      <Table routes={routes} updateDB={updateDB} />
    </div>
  );
}

export default App;
