function autoKeyboard(str) {
    let replacer = {
        "q": "", "w": "", "e": "", "r": "", "t": "", "y": "", "u": "",
        "i": "", "o": "", "p": "", "[": "", "]": "", "a": "", "s": "",
        "d": "", "f": "", "g": "", "h": "", "j": "", "k": "", "l": "",
        ";": "", "'": "", "z": "", "x": "", "c": "", "v": "", "b": "",
        "n": "", "m": "", ",": "", ".": "", "/": "."
    };

    return str.replace(/[A-z/,.;'\][]/g, function (x) {
        return x === x.toLowerCase() ? replacer[x] : replacer[x.toLowerCase()].toUpperCase();
    });
}

export default autoKeyboard