import { useEffect, useState } from "react"
import cities from '../../db.json'
import { findNearest, getDistance } from "geolib";
import './row.css';
import mapLogo from './../../assets/map-logo.png'
import Route from "../Route/route";
import convertTimestamp from "../../utils/convertDate"
import drivers from "../../drivers.json"

function Row({ truck, index, setCoords, setDisplayMap, isToggled, deleteRow, updateDB }) {

    const [distance, setDistance] = useState(0)

    const [closestLoc, setClosestLoc] = useState('')

    const [currentCoords, setCurrentCoords] = useState({})

    const [geo, setGeo] = useState([])

    const [date, setDate] = useState(truck.uploading ? truck.uploading : 0)

    const [routes, setRoutes] = useState([])

    const [dopog, setDopog] = useState(false)

    const [comment, setComment] = useState("")

    const [driver, setDriver] = useState(truck.driver ? truck.driver : "?")

    const [editDriver, setEditDriver] = useState(false)

    useEffect(() => {
        if (truck.latitude) {
            if (isToggled) {
                setGeo(cities.filter(city => {
                    if (city.peoples > 25000) {
                        return city
                    } else {
                        return 0
                    }
                }))
            } else {
                setGeo(cities)
            }
            let { latitude, longitude } = truck;
            setCurrentCoords({ latitude, longitude })
        }
        setDate(truck.uploading)

    }, [truck, isToggled])

    useEffect(() => {
        setComment(truck.comment ? truck.comment : "")
        setDriver(truck.driver ? truck.driver : "?")
    }, [truck])

    useEffect(() => {
        for (let item of drivers) {
            if (item.driver === driver) {
                setDopog(item.dopog ? "" : "")
                break
            }
        }
    }, [driver])

    useEffect(() => {
        if (currentCoords) {
            let nearest = findNearest(currentCoords, geo)
            setClosestLoc(nearest)
            if (nearest) {
                let lat = nearest.latitude
                let lon = nearest.longitude
                let distance = getDistance(currentCoords, { lat, lon })
                setDistance(`${~~(distance / 1000)} `)
            }
        }
    }, [currentCoords, geo, truck])


    const mapHandler = (e) => {
        if (truck.number === ' 070  102') {
            setCoords([-1, -1])
        } else {
            setCoords([currentCoords.latitude, currentCoords.longitude])
        }
        setDisplayMap(true)
    }

    const dateHandler = (e) => {
        let uploadingDate = Date.parse(e.target.value)
        setDate(Date.parse(e.target.value))
        updateDB(truck.number.substr(2, 3), { routes, uploadingDate, comment, driver })
    }

    const commentHandler = (e) => {
        let uploadingDate = date
        updateDB(truck.number.substr(2, 3), { routes, uploadingDate, comment, driver })
    }

    const driverHandler = (e) => {
        setEditDriver(false)
        let uploadingDate = date
        updateDB(truck.number.substr(2, 3), { routes, uploadingDate, comment, driver })
    }

    const isBase = truck.latitude > 54.857 && truck.latitude < 54.859 && truck.longitude > 56.098 && truck.latitude < 56.0995

    return <tr className="row" style={{ backgroundColor: isBase ? 'rgba(169, 189, 245, 0.27)' : date < Date.now() && !isBase ? 'rgba(84, 6, 6, 0.2)' : '' }}>
        <td className="cell n" onDoubleClick={() => deleteRow(index)}>{index + '.'}</td>
        <td className="cell driver" onDoubleClick={() => setEditDriver(true)}>
            {
                editDriver
                    ? <select
                        className="driver-select"
                        onKeyUp={(e) => {
                            if (e.key === 'Enter') {
                                driverHandler()
                            }
                        }}
                        autoFocus={true}
                        onBlur={driverHandler}
                        value={driver}
                        onChange={(e) => setDriver(e.target.value)}
                    >
                        {
                            truck.drivers.map(driver => (
                                <option>{driver}</option>
                            ))
                        }
                    </select>
                    : <div>
                        {driver}
                    </div>
            }
        </td>
        <td className="cell">
            <div className="number">
                <div className="number-val">{truck.number}</div>
                <div className="tooltip number-info">
                    <span className="tooltiptext">
                        <div>
                            {`: ${truck.speed ? truck.speed : ''}`}
                        </div>
                        <div>
                            {`: ${dopog}`}
                        </div>
                        <div>
                            {`. : ${truck.rub ? '' : ''}`}
                        </div>
                    </span>
                </div>
            </div>
        </td>
        <td className="cell">
            <Route truck={truck} updateDB={updateDB} routes={routes} setRoutes={setRoutes} comment={comment} uploadingDate={date} driver={driver} />
        </td>
        <td className="cell">
            <input type="date" className="date" value={convertTimestamp(date)} onChange={dateHandler} />
        </td>
        <td className="cell current-locality">
            {truck.number === ' 070  102'
                ? '' : currentCoords.latitude === -1 ? '' : !isBase
                    ? `${truck.latitude ? closestLoc?.locality_name + ` (${distance})` : ''} ` : ''}
        </td>
        <td className="cell comment-cell">
            <div className="comment">
                <textarea
                    value={comment}
                    onChange={e => setComment(e.target.value)}
                    onBlur={commentHandler}
                />
            </div>
        </td>
        <td className="cell">
            {
                (truck.number === ' 070  102' || truck?.longitude !== -1) && <img src={mapLogo} alt="" className="cell logo" onClick={mapHandler} />
            }
        </td>
    </tr >
}


export default Row