import Autocomplete from './Autocomplete/autocomplete';
import './route.css'
import db from '../../db.json'
import { useEffect, useRef, useState } from 'react';
import SortableList from './sortableList';
import { arrayMoveImmutable } from 'array-move';
import { useWindowSize } from '../../customHooks/useWindowSize';

function Route({ truck, updateDB, routes, setRoutes, comment, driver, uploadingDate }) {

    const [showInput, setShowInput] = useState(false)

    const [width,] = useWindowSize()

    const myRef = useRef()

    useEffect(() => {
        if (truck.route) {
            setRoutes(truck.route)
        }
    }, [truck, setRoutes])

    const addHandler = e => {
        setShowInput(true)
        const scroll = myRef.current.scrollHeight - myRef.current.clientHeight;
        myRef.current.scrollTo(0, scroll);
    }

    const deleteHandler = e => {
        setRoutes(prevState => {
            let routes = prevState.filter((state, i) => i !== 0)
            updateDB(truck.number.substr(2, 3), { routes, uploadingDate, comment, driver })
            return routes
        })
    }

    const onSortEnd = ({ oldIndex, newIndex }) => {
        setRoutes(prevItem => {
            let routes = arrayMoveImmutable(prevItem, oldIndex, newIndex)
            if (prevItem.join() !== routes.join()) {
                updateDB(truck.number.substr(2, 3), { routes, uploadingDate, comment, driver })
            }
            return (arrayMoveImmutable(prevItem, oldIndex, newIndex))
        });
    };

    return <div className="routeWrapper" ref={myRef} >
        <div
            onClick={deleteHandler}
            className='minus'>
            <svg xmlns="http://www.w3.org/2000/svg" version="1.1" x="0px" y="0px" viewBox="0 0 52 52">
                <path d="M26,0C11.664,0,0,11.663,0,26s11.664,26,26,26s26-11.663,26-26S40.336,0,26,0z M26,50C12.767,50,2,39.233,2,26 S12.767,2,26,2s24,10.767,24,24S39.233,50,26,50z" />
                <path d="M39,25H13c-0.552,0-1,0.447-1,1s0.448,1,1,1h26c0.552,0,1-0.447,1-1S39.552,25,39,25z" />
            </svg>
        </div>
        <div className='routes'>
            {
                width < 700
                    ? <div className='route' style={{ flexDirection: 'column' }}>
                        {
                            routes.map((value, index) => (
                                <div className='route' key={index}>
                                    {
                                        index > 0 && <span unselectable="on" className='routeArrow'></span>
                                    }
                                    <span unselectable="on" >{value}</span>
                                </div>
                            ))
                        }
                    </div>
                    : <div style={{ display: showInput ? 'none' : 'flex' }}>
                        <SortableList items={routes} onSortEnd={onSortEnd} axis={'x'} />
                    </div>
            }
            {
                showInput &&
                <Autocomplete
                    myRef={myRef}
                    updateDB={updateDB}
                    suggestions={db.map(loc => loc.locality_name)}
                    setRoutes={setRoutes}
                    routes={routes}
                    truck={truck}
                    setShowInput={setShowInput}
                    uploadingDate={uploadingDate}
                    comment={comment}
                    driver={driver}
                />

            }
        </div>
        <div
            onClick={addHandler}
            className='plus'>
            <svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 52 52">
                <path d="M26,0C11.664,0,0,11.663,0,26s11.664,26,26,26s26-11.663,26-26S40.336,0,26,0z M26,50C12.767,50,2,39.233,2,26 S12.767,2,26,2s24,10.767,24,24S39.233,50,26,50z" />
                <path d="M38.5,25H27V14c0-0.553-0.448-1-1-1s-1,0.447-1,1v11H13.5c-0.552,0-1,0.447-1,1s0.448,1,1,1H25v12c0,0.553,0.448,1,1,1 s1-0.447,1-1V27h11.5c0.552,0,1-0.447,1-1S39.052,25,38.5,25z" />
            </svg>
        </div>
    </div >
}


export default Route;