import React from "react"
import ContentLoader from "react-content-loader"

const Loader = (props) => {

    return <ContentLoader
        width={props.width}
        speed={2}
        height={30}
        backgroundColor="#f3f3f3"
        foregroundColor="#ecebeb"
        {...props}
    >
        <rect x="3" y="5" rx="0" ry="0" height="24" width="200" />
    </ContentLoader>
}

export default Loader
