import React, { useEffect, useState } from "react";
import "./notification.css";
import drivers from "../../drivers.json"
import trucks from "../../trucks.json"

function Notification() {

    const [birthdayCheck, setBirthdayCheck] = useState(document.cookie.indexOf('birthdayCheck=') !== -1)
    const [dopogCheck, setDopogCheck] = useState(document.cookie.indexOf('dopogCheck=') !== -1)

    const [bithdayDriverTd, setBirthdayDriverTd] = useState('')
    const [bithdayDriverTr, setBirthdayDriverTr] = useState('')

    const [dopogNote, setDopogNote] = useState('')

    const clickHandlerBirthday = () => {
        let midnight = new Date();
        midnight.setHours(23, 59, 59, 0);
        document.cookie = "birthdayCheck=true; expires=" + midnight.toGMTString()
        setBirthdayCheck(true)
    }

    const clickHandlerDopog = () => {
        let midnight = new Date();
        midnight.setHours(23, 59, 59, 0);
        document.cookie = "dopogCheck=true; expires=" + midnight.toGMTString()
        setDopogCheck(true)
    }

    useEffect(() => {
        if (!birthdayCheck) {
            let today = new Date();
            let dd = String(today.getDate()).padStart(2, '0');
            let mm = String(today.getMonth() + 1).padStart(2, '0');
            today = dd + '.' + mm
            let birthdayBoys = []
            for (let driver of drivers) {
                if (driver.birthday.substring(0, 5) === today) {
                    birthdayBoys.push({ name: driver.driver, day: 'today' })
                } else if (('0' + (driver.birthday.substring(0, 2) - 1)).slice(-2) + driver.birthday.substring(2, 5) === today) {
                    birthdayBoys.push({ name: driver.driver, day: 'tomorrow' })
                }
            }

            birthdayBoys.filter(boy => boy.day === 'today').forEach(boy => {
                setBirthdayDriverTd(prevState => prevState + (prevState.length ? '  ' + boy.name : '    ' + boy.name))
            })
            birthdayBoys.filter(boy => boy.day === 'tomorrow').forEach(boy => {
                setBirthdayDriverTr(prevState => prevState + (prevState.length ? '  ' + boy.name : '    ' + boy.name))
            })
            if (birthdayBoys.length === 0) {
                clickHandlerBirthday()
            }
        }
        if (!dopogCheck) {
            let dopogFinish = []
            setDopogNote('')
            for (let truck of trucks) {
                if (truck.dopog) {
                    let myDate = truck.dopog.split(".");
                    if (Date.now() > new Date(myDate[2], myDate[1] - 1, myDate[0]).getTime() - 1728000000) {
                        dopogFinish.push({ number: truck.number, date: truck.dopog })
                    }
                }
            }
            if (dopogFinish.length === 0) {
                clickHandlerDopog()
            } else {
                dopogFinish.forEach(dopog => setDopogNote(prevState => prevState + (prevState ? "  " : "") + ("  " + dopog.number + '  ' + dopog.date)))
            }
        }
    }, [birthdayCheck, dopogCheck])

    return <>
        <div className="notification" style={{ display: birthdayCheck ? 'none' : 'block' }}>
            <div className="birthday">
                {
                    bithdayDriverTd + (bithdayDriverTd.length ? "! " : '')
                }
                {
                    bithdayDriverTr
                }
            </div>

            <div className="confirm" onClick={() => clickHandlerBirthday()}>
                OK
            </div>
        </div>
        <div className="dopog" style={{ "display": dopogCheck ? 'none' : 'flex' }}>
            <div>
                {'  ' + dopogNote}
            </div>
            <div className="close-dopog" onClick={() => clickHandlerDopog()}>
                <svg width="9" height="9" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fillRule="white" clipRule="evenodd" d="M7.42618 6.00003L11.7046 1.72158C12.0985 1.32775 12.0985 
                        0.689213 11.7046 0.295433C11.3108 -0.0984027 10.6723 -0.0984027 
                        10.2785 0.295433L5.99998 4.57394L1.72148 0.295377C1.32765 -0.098459
                        0.68917 -0.098459 0.295334 0.295377C-0.0984448 0.689213 -0.0984448 
                        1.32775 0.295334 1.72153L4.57383 5.99997L0.295334 10.2785C-0.0984448 10.6723 
                        -0.0984448 11.3108 0.295334 11.7046C0.68917 12.0985 1.32765 12.0985 1.72148 11.7046L5.99998 
                        7.42612L10.2785 11.7046C10.6723 12.0985 11.3108 12.0985 11.7046 11.7046C12.0985 11.3108 
                        12.0985 10.6723 11.7046 10.2785L7.42618 6.00003Z" fill="#FFF" />
                </svg>
            </div>
        </div>
    </>
};


export default Notification
