import React, { useEffect, useState } from "react";
import API from "../../api";
import Row from "../Row/row";
import trucksDB from "../../trucks.json"
import './table.css';
import Popup from "../Popup/popup";
import ToggleSwitch from "../ToggleSwitch/toggleSwitch";
import IFRAME from "../Popup/iframe";
import Loader from "../Loader/loader";
import Notification from "../Notification/notification";
import { useWindowSize } from "../../customHooks/useWindowSize";

function Table({ routes, updateDB }) {

    const [width,] = useWindowSize()

    const [fetch, setFetch] = useState(false)

    const [displayMap, setDisplayMap] = useState(false)

    const [coords, setCoords] = useState([55, 55])

    const [isToggled, setIsToggled] = useState(localStorage.getItem('isToggled') ? JSON.parse(localStorage.getItem('isToggled')) : true)

    const [trucks, setTrucks] = useState([])

    const [updateTime, setUpdateTime] = useState()

    const [owner, setOwner] = useState(localStorage.getItem('owner') ? parseInt(localStorage.getItem('owner')) : "0")

    const [allTrucks, setAllTrucks] = useState([])

    const sortNumbersUp = (e) => {
        localStorage.setItem('sort', 1)
        setTrucks([...trucks.sort((a, b) => a.number.substr(2, 3) - b.number.substr(2, 3))])
    }
    const sortNumbersDown = (e) => {
        localStorage.setItem('sort', 2)
        setTrucks([...trucks.sort((a, b) => b.number.substr(2, 3) - a.number.substr(2, 3))])
    }
    const sortDriverUp = (e) => {
        localStorage.setItem('sort', 3)
        setTrucks([...trucks.sort((a, b) => a.driver.localeCompare(b.driver))])
    }
    const sortDriverDown = (e) => {
        localStorage.setItem('sort', 4)
        setTrucks([...trucks.sort((a, b) => b.driver.localeCompare(a.driver))])
    }
    const sortDateUp = (e) => {
        localStorage.setItem('sort', 5)
        setTrucks([...trucks.sort((a, b) => a.uploading - b.uploading)])
    }
    const sortDateDown = (e) => {
        localStorage.setItem('sort', 6)
        setTrucks([...trucks.sort((a, b) => b.uploading - a.uploading)])
    }

    const sortCarsByOwner = () => {
        if (owner < 2) {
            setOwner(prevState => prevState + 1)
            localStorage.setItem("owner", owner + 1)
        } else {
            setOwner(0)
            localStorage.setItem("owner", 0)
        }
    }


    useEffect(() => {
        let cars = ['126', '366', '017', '044', '332', '392']
        if (allTrucks.length > 0) {
            if (parseInt(owner) > 0) {
                sortTrucks(allTrucks.filter(truck => {
                    if (cars.includes(truck.number.substr(2, 3))) {
                        return owner === 1 ? false : true
                    } else {
                        return owner === 1 ? true : false
                    }
                }))
            } else {
                sortTrucks(allTrucks)
            }
        }
    }, [owner, allTrucks])

    const sortTrucks = (trucksToSort) => {
        if (localStorage.getItem('sort') === "1" || !localStorage.getItem('sort')) {
            setTrucks([...trucksToSort.sort((a, b) => a.number.substr(2, 3) - b.number.substr(2, 3))])
        } else if (localStorage.getItem('sort') === "2") {
            setTrucks([...trucksToSort.sort((a, b) => b.number.substr(2, 3) - a.number.substr(2, 3))])
        } else if (localStorage.getItem('sort') === "3") {
            setTrucks([...trucksToSort.sort((a, b) => a.driver.localeCompare(b.driver))])
        } else if (localStorage.getItem('sort') === "4") {
            setTrucks([...trucksToSort.sort((a, b) => b.driver.localeCompare(a.driver))])
        } else if (localStorage.getItem('sort') === "5") {
            setTrucks([...trucksToSort.sort((a, b) => a.uploading - b.uploading)])
        } else if (localStorage.getItem('sort') === "6") {
            setTrucks([...trucksToSort.sort((a, b) => b.uploading - a.uploading)])
        }
    }

    const deleteRow = (index) => {
        setAllTrucks(prevState => prevState.filter((el, i) => i !== index - 1))
    }

    useEffect(() => {
        let trucksInfo = [...trucksDB]
        API.getCoords()
            .then(data => {
                if (data.length) {
                    for (let i = 0; i < data.length; i++) {
                        trucksInfo[i].latitude = data[i].coord.y
                        trucksInfo[i].longitude = data[i].coord.x
                        trucksInfo[i].speed = data[i].coord.speed
                    }
                }
                return data
            })
            .catch(data => {
                for (let i = 0; i < trucksDB.length; i++) {
                    trucksInfo[i].latitude = -1
                    trucksInfo[i].longitude = -1
                    trucksInfo[i].speed = 0
                }
            })
            .finally(() => {
                setFetch(true)
                if (Object.keys(routes).length > 0) {
                    for (let i = 0; i < trucksDB.length; i++) {
                        trucksInfo[i].route = routes[trucksDB[i].number.substr(2, 3)].routes
                        trucksInfo[i].uploading = routes[trucksDB[i].number.substr(2, 3)].uploadingDate
                        trucksInfo[i].comment = routes[trucksDB[i].number.substr(2, 3)].comment
                        trucksInfo[i].driver = routes[trucksDB[i].number.substr(2, 3)].driver
                    }
                    setAllTrucks(trucksInfo)
                    sortTrucks(trucksInfo)
                }
            })
    }, [routes])


    useEffect(() => {
        setUpdateTime(`${('0' + new Date().getHours()).slice(-2)}:${('0' + new Date().getMinutes()).slice(-2)}`)
    }, [])

    return <div>
        <Notification />
        <table className="table">
            <thead>
                <tr>
                    <th className="n" onDoubleClick={() => sortCarsByOwner()}></th>
                    <th className="driver">
                        <div className="header">
                            
                            <div>
                                <div className="arrow" onClick={sortDriverUp}></div>
                                <div className="arrow" onClick={sortDriverDown}></div>
                            </div>
                        </div>
                    </th>
                    <th>
                        <div className="header">
                            
                            <div>
                                <div className="arrow" onClick={sortNumbersUp}></div>
                                <div className="arrow" onClick={sortNumbersDown}></div>
                            </div>
                        </div>
                    </th>
                    <th></th>
                    <th>
                        <div className="header"> 
                            <div>
                                <div className="arrow" onClick={sortDateUp}></div>
                                <div className="arrow" onClick={sortDateDown}></div>
                            </div>
                        </div>
                    </th>
                    <th className="current-locality">
                        <div>
                            {`
                                  ${updateTime}
                            `}
                        </div>
                        <div className="show-cities">
                             
                            <div className="toggle">
                                <ToggleSwitch isToggled={isToggled} setIsToggled={setIsToggled} />
                            </div>
                        </div>
                    </th>
                    <th className="comment-header">
                        <div className="header"></div>
                    </th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                {
                    fetch && allTrucks.length > 0
                        ? trucks.map((truck, i) => (
                            <Row
                                updateDB={updateDB}
                                isToggled={isToggled}
                                key={i}
                                index={i + 1}
                                truck={truck}
                                setDisplayMap={setDisplayMap}
                                setCoords={setCoords}
                                deleteRow={deleteRow}
                            />
                        ))
                        : <>
                            {
                                trucksDB.map((truck, i) => (
                                    <tr key={i + 'tr'}>
                                        {
                                            Array.apply(null, Array(width < 700 ? 4 : width < 1000 ? 7 : 8)).map((el, i) => (
                                                <td key={i + 'td'}>
                                                    <Loader width={width > 0 ? width / (width < 700 ? 4 : width < 1000 ? 7 : 8) - 15 : 100} />
                                                </td>
                                            ))
                                        }
                                    </tr>
                                ))
                            }
                        </>
                }
            </tbody>
        </table>
        {
            coords[0] !== -1 && coords[1] !== -1
                ? <Popup
                    coords={coords}
                    displayMap={displayMap}
                    setDisplayMap={setDisplayMap}
                />
                : <IFRAME
                    displayMap={displayMap}
                    setDisplayMap={setDisplayMap}
                />
        }
    </div >
}

export default Table;